export default {
	data() {
		return {
			StatusBar: this.StatusBar,
			CustomBar: 50
		};
	},
	name: 'cu-custom',
	computed: {
		style() {
			var StatusBar= this.StatusBar;
			var CustomBar= this.CustomBar;
			var bgImage = this.bgImage;
			var Color = this.Color
			var backColor = this.backColor
			var style = `height:${CustomBar}px;padding-top:${StatusBar}px;color:${Color};background-color:${backColor}`;
			if (this.bgImage) {
				style = `${style}background-image:url(${bgImage});color:${Color};background-color:${backColor}`;
			}
			return style
		}
	},
	props: {
		bgColor: {
			type: String,
			default: ''
		},
		isBack: {
			type: [Boolean, String],
			default: false
		},
		isIcon: {
			type: [Boolean, String],
			default: false
		},
		isBacks: {
			type: [Boolean, String],
			default: false
		},
		isIcons: {
			type: [Boolean, String],
			default: false
		},
		bgImage: {
			type: String,
			default: ''
		},
		Color: {
			type: String,
			default: ''
		},
		backColor: {
			type: String,
			default: 'red'
		},
	},
	methods: {
		BackPage() {
			uni.navigateBack({
				delta: 1
			});
		},
		goRight() {
			this.$emit('goRight')
		},
		Back() {
			this.$emit('Back')
		}
	}
}
